.InputErrors {
  position: absolute;
  top: -44px;
  box-sizing: border-box;
  margin: 0;
  padding: 4px 8px;
  line-height: 24px;
  color: #fc0303;
  border: solid 1px #fc0303;
  border-radius: 10px;
}

.InputErrors--animate-run { 
  visibility: hidden; 
  transition: all 1s ease-out;
}